<template>
  <div class="auth-wrapper auth-v1 px-2">
    <div class="auth-inner py-2">

      <!-- Forgot Password v1 -->
      <b-card class="mb-0">
        <b-link class="brand-logo">
          <!-- logo -->
          <b-img
            width="50px"
            :src="appLogoImage"
            alt="logo"
          />
          <!--          <vuexy-logo />-->

          <h2 class="brand-text text-primary ml-1">
            التحقق من البريد الالكتروني
          </h2>
        </b-link>

        <b-card-title class="mb-1">
          {{ $t('pages.verified.title') }}
        </b-card-title>
        <b-card-text class="mb-2">
          {{ $t('pages.verified.description') }}
        </b-card-text>

        <!-- form -->
        <validation-observer ref="simpleRules">
          <b-form
            class="auth-forgot-password-form mt-2"
            @submit.prevent="validationForm"
          >
            <!-- email -->
            <b-form-group
              :label="$t('forms.verified.code')"
              label-for="forgot-password-email"
            >
              <validation-provider
                #default="{ errors }"
                name="code"
                rules="required"
              >
                <b-form-input
                  id="verification"
                  v-model="pinCode"
                  :state="errors.length > 0 ? false:null"
                  name="verification"
                  placeholder=""
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>

            <!-- submit button -->
            <b-button
              variant="primary"
              block
              type="submit"
            >
              {{ $t('forms.verified.sendCode') }}
            </b-button>
          </b-form>
        </validation-observer>

        <b-card-text class="text-center mt-2">
          <b-link :to="{name:'login'}">
            <feather-icon icon="ChevronLeftIcon" />
            {{ $t('forms.verified.login') }}
          </b-link>
        </b-card-text>

      </b-card>
    <!-- /Forgot Password v1 -->
    </div>
  </div>
</template>

<script>
import { ValidationProvider, ValidationObserver } from 'vee-validate'
// import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BCard, BLink, BCardText, BCardTitle, BFormGroup, BFormInput, BForm, BButton, BImg,
} from 'bootstrap-vue'
import { required, email } from '@validations'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import router from '@/router'
import { $themeConfig } from '@themeConfig'

export default {
  components: {
    // VuexyLogo,
    BCard,
    BLink,
    BCardText,
    BCardTitle,
    BFormGroup,
    BFormInput,
    BButton,
    BForm,
    BImg,
    ValidationProvider,
    ValidationObserver,
  },
  data() {
    return {
      appLogoImage: $themeConfig.app.appLogoImage,
      pinCode: '',
      userData: JSON.parse(localStorage.getItem('info')) ?? {},
      // validation
      required,
      email,
    }
  },
  methods: {
    validationForm() {
      this.$refs.simpleRules.validate().then(success => {
        if (success) {
          this.$store
            .dispatch('auth/verified', {
              email: this.userData ? this.userData.email : null,
              pin_code: this.pinCode,
            })
            .then(() => {
              router.push('contract/create')
              this.$toast({
                component: ToastificationContent,
                props: {
                  title: this.$t('forms.verified.message'),
                  icon: 'EditIcon',
                  variant: 'success',
                },
              })
            })
        }
      })
    },
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/pages/page-auth.scss';
</style>
